/* global bootstrap */

import md5 from 'md5';
import Storage from './Storage'
import Util from './Util';


const TAB_ID_KEY = 'data-tab-id';
const TAB_URL_KEY = 'data-tab-url';

//命名空间
const TAB_NAMESPACE = 'quicktab';

// tab激活的类名
const TAB_ACTIVE_CLASS_NAME = 'active';

// tab滚动区域的类名
const TAB_SCROLL_AREA_CLASS_NAME = 'scroll_area';

// tab全屏类
const TAB_FULLSCREEN_CLASS_NAME = 'qt-fullscreen';

// tab隐藏类
const TAB_PANE_HIDE_CLASS_NAME = 'outing';

// tab关闭图标挂载类名
const TAB_CLOSE_BTN_ICON_CLASS_NAME = 'qt-close';

// 用于tab右键菜单时给容器挂载的阻止鼠标事件的类
const TAB_CONTEXTMENU_POINTER_EVENTS_CLASS_NAME = 'pen';

// 用于更多下拉菜单展开时给容器挂载的阻止鼠标事件的类
const TAB_MORE_ACTION_DROPDOWNS_POINTER_EVENTS_CLASS_NAME = 'more-menu-pen';

// 全屏触发class
const TAB_FULLSCREEN_TRIGGER_CLASS_NAME = 'fullscreen';


// 固定宽度nav-link
const TAB_FIXED_WIDTH_NAV_LINK_CLASS_NAME = 'iconbtn';

// 全屏模板
const TAB_FULLSCREEN_TPL = `
        <!-- 全屏实现 -->
        <li class="nav-item  border-start">
            <button class="nav-link ${TAB_FIXED_WIDTH_NAV_LINK_CLASS_NAME} ${TAB_FULLSCREEN_TRIGGER_CLASS_NAME}" >
                <i class="bi bi-arrows-fullscreen"></i>
            </button>
        </li>
`;

// tab的主体模板
const TAB_MAIN_TPL = `
            <ul class="nav nav-pills border flex-nowrap">
                <!--                左边的箭头-->
                <li class="nav-item border-end" >
                    <button class="nav-link ${TAB_FIXED_WIDTH_NAV_LINK_CLASS_NAME} move-left-btn" ><i class="bi bi-caret-left"></i></button>
                </li>
                <!--                中间滚动的区域-->
                <li class="nav-item flex-grow-1">
                    <div class="${TAB_SCROLL_AREA_CLASS_NAME}"></div>
                </li>
                <!--                右边的箭头-->
                <li class="nav-item  border-start" >
                    <button class="nav-link ${TAB_FIXED_WIDTH_NAV_LINK_CLASS_NAME} move-right-btn" >
                        <i class="bi bi-caret-right"></i>
                    </button>
                </li>
                <!--                操作区域-->
                <li class="nav-item  border-start dropdown" >
                    <button class="nav-link ${TAB_FIXED_WIDTH_NAV_LINK_CLASS_NAME}" data-bs-toggle="dropdown" >
                        <i class="bi bi-caret-down"></i>
                    </button>
                    <ul class="dropdown-menu dropdown-menu-end">
                        <li>
                            <button class="dropdown-item rollback-current" type="button">回到当前</button>
                        </li>
                        <li>
                            <button class="dropdown-item refresh-current" type="button">刷新当前</button>
                        </li>
                        <li>
                            <button class="dropdown-item close-current" type="button">关闭当前</button>
                        </li>
                        <li>
                            <button class="dropdown-item close-other" type="button">关闭其他</button>
                        </li>
                        <li>
                            <button class="dropdown-item close-all" type="button">关闭全部</button>
                        </li>
                    </ul>
                </li>
                {{tab_fullscreen}}
            </ul>
                
            <!-- 面板容器 -->
            <div class="flex-grow-1 border-top-0 border">
            </div>
`


// 单个tab
const TAB_TPL = `<button class="nav-link flex-shrink-0 border-end position-relative" type="button" ${TAB_URL_KEY}="{{url}}"  ${TAB_ID_KEY}="{{id}}">{{title}}</button>`;
// 携带关闭按钮的tab
const TAB_WITH_CLOSE_TPL = `<button class="nav-link flex-shrink-0  border-end position-relative" type="button" ${TAB_URL_KEY}="{{url}}"  ${TAB_ID_KEY}="{{id}}">{{title}}<i class="bi bi-x ${TAB_CLOSE_BTN_ICON_CLASS_NAME}"></i></button>`;

// tab的遮罩层
const TAB_LOADING_TPL = `
     <!--loading-->
     <div class="position-absolute start-0 end-0 top-0 bottom-0 bg-body-tertiary show d-flex align-items-center justify-content-center">
         <div class="spinner-border text-body-tertiary" role="status">
         <span class="visually-hidden">Loading...</span>
         </div>
    </div>
`;


//tab面板模板
const TAB_PANE_TPL = `
<!--每个面板-->
<div class="h-100 w-100 position-relative" ${TAB_ID_KEY}="{{id}}">
    ${TAB_LOADING_TPL}    
    <iframe src="{{url}}" class="d-block w-100 h-100"></iframe>
</div>
`;


//右键菜单的容器模板
const TBA_CONTEXTMENU_TPL = `
            <div class="list-group ${TAB_NAMESPACE}-contextmenu shadow-sm">
                <button type="button" class="list-group-item list-group-item-action refresh">刷新</button>
                <button type="button" class="list-group-item list-group-item-action close">关闭</button>
                <button type="button" class="list-group-item list-group-item-action close-other">关闭其它</button>
            </div>`;


// 默认选项
const DEFAULT = {
    //dom选择器
    selector: null,
    //最小高度，默认值是222px
    minHeight: '',
    //高度 默认高度是自适应100%
    height: '',
    //宽度 默认宽度是自适应100%
    width: '',
    //缓存 null:不缓存  sessionStorage  localStorage
    cache: null,
    //初始tab
    tabs: [],
    //启用全屏功能
    enableFullscreen: false,
    //启用tab右键菜单
    enableContextmenu: false,
    //启用鼠标滚动切换tab
    enableMouseWheelToggleTab: false
}


//单个tab的默认选项
const TABDEFAULT = {
    //标题
    title: '',
    //连接
    url: '',
    //tab唯一标志
    id: null,
    //是否可以关闭
    close: true
}


class Quicktab {


    static instances = [];

    constructor(option) {
        this._option = Object.assign({}, DEFAULT, option)

        //每个tab的id是不一样的
        this._tab_id = md5(this._option.selector);
        this._tab_cache_key = `${TAB_NAMESPACE}-` + this._tab_id;


        //构建样式
        this._buildStyle();
        //缓存初始化
        this.storage = this._initCache();
        // tab初始化
        this._init();

        //静态属性存实例对象,用于子页面快速找到父级页面实例，然后添加tab等..
        Quicktab.instances[this._tab_id] = this;

    }


    _initCache() {
        if (this._option.cache === 'localStorage') {
            return new Storage(2);
        } else if (this._option.cache === 'sessionStorage') {
            return new Storage(1);
        } else {
            return null;
        }
    }


    //静态方法
    static getTabId(tabEl) {
        return tabEl.getAttribute(TAB_ID_KEY)
    }

    static getTabUrl(tabEl) {
        return tabEl.getAttribute(TAB_URL_KEY)
    }

    static canAccessIFrame(iframe) {
        return Util.canAccessIFrame(iframe);
    }


    static getTabIdByUrl(url) {
        return md5(url);
    }


    // static get(selector) {
    //
    // }


    _buildStyle() {
        let styleEl = document.querySelector(`style[namespace="${TAB_NAMESPACE}"]`);
        if (!(styleEl instanceof HTMLStyleElement)) {
            let style = document.createElement('style');
            style.setAttribute('namespace', TAB_NAMESPACE);
            style.textContent = `
                .${TAB_NAMESPACE}{
                    height:222px;
                    background-color: var(--bs-body-bg);
                    display: flex;
                    flex-direction: column;
                }
            
                /*  面板移除样式 */
                .${TAB_NAMESPACE} .${TAB_PANE_HIDE_CLASS_NAME} {
                    position: absolute !important;
                    transform: translateX(-200%);
                }
            
                /*去除所有的圆角*/
                .${TAB_NAMESPACE} .nav-pills .nav-link {
                    border-radius: 0;
                }
            
                .${TAB_NAMESPACE} .nav-pills .nav-link .${TAB_CLOSE_BTN_ICON_CLASS_NAME} {
                    position: absolute;
                    top: 0;
                    right: 0;
                    display: none;
                    cursor: pointer;
                }
            
                .${TAB_NAMESPACE} .nav-pills .nav-link .${TAB_CLOSE_BTN_ICON_CLASS_NAME}:hover {
                    color: #f00;
                }
            
                .${TAB_NAMESPACE} .nav-pills .nav-link:hover .${TAB_CLOSE_BTN_ICON_CLASS_NAME} {
                    display: inline;
                }
                
                
                .${TAB_NAMESPACE} li.flex-grow-1 {
                    width:0;
                }
                /*必须添加该样式否则滚动条会超出*/
                .${TAB_NAMESPACE} div.flex-grow-1 {
                    overflow: hidden;
                }
                
         
                .${TAB_NAMESPACE} li.flex-grow-1 .${TAB_SCROLL_AREA_CLASS_NAME}{
                    position: relative;
                    display:flex;
                    flex-wrap: nowrap;
                    align-items: center;
                    scroll-behavior:smooth;
                    overflow: scroll;
                    scrollbar-width: none;
                    -ms-overflow-style: none;
                }
                
                  /* 谷歌浏览器 */
                .${TAB_NAMESPACE} li.flex-grow-1 .${TAB_SCROLL_AREA_CLASS_NAME}::-webkit-scrollbar {
                     display: none;
                }
                
                /*中间的tab设置一个最大的宽度超出部分显示省略号*/
                .${TAB_NAMESPACE} li.flex-grow-1 .nav-link {
                    min-width: 0;
                    max-width: 160px;
                    white-space: nowrap;
                    text-overflow: ellipsis;
                    overflow: hidden;
                }
            
                /*给图标按钮设置一个固定宽度，否则滚动到指定tab位置时不精确*/
                .${TAB_NAMESPACE} .${TAB_FIXED_WIDTH_NAV_LINK_CLASS_NAME}{
                    width: 48px;
                }
            
                .${TAB_NAMESPACE} .show {
                    opacity: 1;
                }
            
                .${TAB_NAMESPACE} .hide {
                    opacity: 0;
                    transition: opacity 800ms;
                }
            
                .${TAB_NAMESPACE}.${TAB_FULLSCREEN_CLASS_NAME} {
                    height: 100% !important;
                    width: 100% !important;
                    position: fixed !important;
                    z-index: 99999 !important;
                    top: 0 !important;
                    bottom: 0 !important;
                    left: 0 !important;
                    right: 0 !important;
                }
                
                .${TAB_NAMESPACE}-contextmenu {
                  display: none;
                  position: fixed;
                  width: 100px;
                  background: #fff;
                  z-index: 999999 !important;
                }
                
                
                .${TAB_NAMESPACE}.${TAB_CONTEXTMENU_POINTER_EVENTS_CLASS_NAME} iframe {
                  pointer-events: none;
                }
                
                .${TAB_NAMESPACE}.${TAB_MORE_ACTION_DROPDOWNS_POINTER_EVENTS_CLASS_NAME} iframe {
                  pointer-events: none;
                }
            `;
            document.head.appendChild(style);
        }
    }

    _init() {

        //事件
        this.listeners = [];
        this.handlers = {};

        //容器
        this._tab_container = document.querySelector(this._option.selector);

        //第一步检测是否是一个有效的dom容器
        if (!(this._tab_container instanceof HTMLElement)) {
            throw new Error(`selector '${this._option.selector}' invalid`);
        }


        //第二步,检测是否是一个已经被实例化的dom容器
        if (this._tab_container.classList.contains(TAB_NAMESPACE)) {
            throw new Error(`The provided selector '${this._option.selector}' has been initialized`);
        }

        // 选项可通过data属性覆盖
        this._dataAttrAssign();


        //设置容器的宽高(用于做响应式)
        this._setContainerWH();

        //给容器挂载容器所需的类名
        this._tab_container.classList.add(TAB_NAMESPACE);

        //加入到容器中
        this._tab_container.insertAdjacentHTML('beforeEnd', this._formatMainHtmlStr());


        //tab的容器
        this._tab_wraper = this._tab_container.querySelector(`li.flex-grow-1 > .${TAB_SCROLL_AREA_CLASS_NAME}`);
        //tab面板的容器
        this._tab_pane_wraper = this._tab_container.querySelector('div.flex-grow-1');

        if (this._option.enableContextmenu === true) {
            //构建右键菜单dom
            this._buildContextmenu();
        }

        //事件监听
        this._addEventListener();


        //回显tab(可能是缓存里的，或者是option选项里面的)
        this._restoreTabs();
    }


    addTab(option) {

        //新添加一个id参数
        option.id = md5(option.url);
        //参数合并
        option = Object.assign({}, TABDEFAULT, option)


        if (this.getTabById(option.id) === null) {//如果没有该tab则添加这个tab

            //添加tab
            let tabHtmlStr = this._getFormatTabStr(option);


            this._tab_wraper.insertAdjacentHTML('beforeEnd', tabHtmlStr);

            //添加iframe
            let iframeHtmlStr = this._formatString(TAB_PANE_TPL, option);
            this._tab_pane_wraper.insertAdjacentHTML('beforeEnd', iframeHtmlStr);

            //同时也添加进缓存
            this._addTabToCache(option);

        }


        //激活tab
        this.activeTabById(option.id);

    }


    activeTabById(id) {

        let _this = this;


        //激活缓存中的tab
        _this._activeCacheTabById(id);


        //删除前一个tab的激活样式
        let preActiveTab = this.getActiveTab();
        if (preActiveTab instanceof HTMLElement) {
            preActiveTab.classList.remove(TAB_ACTIVE_CLASS_NAME)
        }


        //给当前tab添加激活样式
        this.getTabById(id).classList.add('active');


        //给面板激活(先给所有的面板都加上.outing类，然后再把当前的面板移除该类)
        for (let tab_pane of this._tab_pane_wraper.children) {
            tab_pane.classList.add(TAB_PANE_HIDE_CLASS_NAME);
        }

        //先查找该面板是否存在(因为可能是惰性tab)
        let tabPane = this.getTabPaneById(id);
        if (!(tabPane instanceof HTMLElement)) {
            //添加面板保证一定有iframe
            this._addTabPaneById(id);
        }

        //给iframe绑定事件
        _this._addIframeLoadEventById(id);

        //给当前的面板移进视野
        this.getTabPaneById(id).classList.remove(TAB_PANE_HIDE_CLASS_NAME);

        //滚动到指定的tab区域
        this.scrollToTabById(id);

    }


    /**
     * 滚动至指定id的tab位置
     * @param id
     */
    scrollToTabById(id) {
        let tab = this.getTabById(id);
        // 获取到当前点击元素的 offsetLeft  - 包裹盒子 offsetWidth 的一半 + 当前点击元素 offsetWidth 的一半
        this._tab_wraper.scrollLeft = tab.offsetLeft - this._tab_wraper.offsetWidth / 2 + tab.offsetWidth / 2;
    }


    getTabById(id) {
        return this._tab_wraper.querySelector(`[data-tab-id="${id}"]`);
    }


    getTabs() {
        return this._tab_wraper.querySelectorAll('button.nav-link');
    }


    getActiveTab() {
        return this._tab_wraper.querySelector('button.nav-link.active');
    }


    getIFrameById(id) {
        return this.getTabPaneById(id).querySelector('iframe');
    }


    getTabPaneById(id) {
        return this._tab_pane_wraper.querySelector(`[data-tab-id="${id}"]`);
    }


    on(type, handler) {
        if (typeof this.handlers[type] === 'undefined') {
            this.handlers[type] = [];
        }
        this.listeners.push(type);
        this.handlers[type].push(handler);
        return this;
    }

    off(type, handler) {
        if (this.handlers[type] instanceof Array) {
            let handlers = this.handlers[type];
            for (let i = 0; i < handlers.length; i++) {
                if (handlers[i] === handler) {
                    this.listeners.splice(i, 1);
                    handlers.splice(i, 1);
                    break;
                }
            }
            return this;
        }
    }


    _emit(event) {
        if (!event.target) {
            event.target = this;
        }
        if (this.handlers[event.type] instanceof Array) {
            return this.handlers[event.type][0](event);
        }
        return false;
    }


    /**
     * 格式化模板数据
     * @param str
     * @param data
     * @returns {*}
     * @private
     */
    _formatString(str, data) {
        return str.replace(/{{(\w+)}}/g, function (matchingStr, group1) {
            return data[group1];
        });
    }


    // 右键菜单的容器构建
    _buildContextmenu() {

        //找到这个容器
        let tab_contextmenu_wraper = document.querySelector(`.${TAB_NAMESPACE}-contextmenu`);

        if (!(tab_contextmenu_wraper instanceof HTMLElement)) {
            //加入到body
            document.body.insertAdjacentHTML('beforeEnd', TBA_CONTEXTMENU_TPL);
        }

        this._tab_contextmenu_wraper = document.querySelector(`.${TAB_NAMESPACE}-contextmenu`);


    }

    //事件监听
    _addEventListener() {

        let _this = this;


        //监听窗口变化也要让右键菜单消失
        window.addEventListener('resize', Util.debounce(function () {

            if (_this._option.enableContextmenu === true) {
                //关闭tab的右键菜单
                _this._closeTabsContextmenu();
            }
            //回到当前位置
            _this.scrollToActiveTab();

            _this._moreMenuDropdownHide();
        }, 150))


        //tab单击事件
        Util.delegate(_this._tab_wraper, 'click', 'button.nav-link', function () {
            _this.activeTabById(Quicktab.getTabId(this));
        }, `.${TAB_CLOSE_BTN_ICON_CLASS_NAME}`);


        //tab关闭
        Util.delegate(_this._tab_wraper, 'click', `.${TAB_CLOSE_BTN_ICON_CLASS_NAME}`, function () {
            _this.closeTabById(Quicktab.getTabId(this.parentNode));
        });


        //全屏
        Util.delegate(_this._tab_container, 'click', `.${TAB_FULLSCREEN_TRIGGER_CLASS_NAME}`, function () {
            if (_this._tab_container.classList.contains(TAB_FULLSCREEN_CLASS_NAME)) {
                _this.exitFullscreen();
            } else {
                _this.fullscreen();
            }
        });


        //左滑动按钮被单击
        Util.delegate(_this._tab_container, 'click', '.move-left-btn', function () {
            _this.leftScroll();
        });


        //右滑动按钮被单击
        Util.delegate(_this._tab_container, 'click', '.move-right-btn', function () {
            _this.rightScroll();
        });


        //回到当前
        Util.delegate(_this._tab_container, 'click', '.rollback-current', function () {
            _this.scrollToActiveTab();
        });


        //刷新当前
        Util.delegate(_this._tab_container, 'click', '.refresh-current', function () {
            _this.refreshActiveTab();
        });


        //更多操作按钮的事件处理
        _this._moreMenuEvent();


        // 通过data属性可以直接快捷添加tab
        Util.delegate(document.body, 'click', '[data-qt-tab][data-qt-target]', function (e) {
            e.preventDefault();

            // console.log(this);

            let dsfds = this.getAttribute('data-qt-target');

            if (dsfds === _this._option.selector) {
                // console.log('3333');

                let tab = Object.assign({}, TABDEFAULT, JSON.parse(this.getAttribute('data-qt-tab')));
                _this.addTab(tab);

            }


        });


        //关闭当前激活tab
        Util.delegate(_this._tab_container, 'click', '.close-current', function () {
            //得到当前激活的id
            _this.closeActiveTab();
        });


        //关闭除了激活tab的其它tab
        Util.delegate(_this._tab_container, 'click', '.close-other', function () {
            _this.closeAlLTabsExceptActiveTab();
        });


        //关闭全部
        Util.delegate(_this._tab_container, 'click', '.close-all', function () {
            _this.closeAllTabs();
        });


        if (_this._option.enableContextmenu === true) {
            //注册tab右键菜单的相关事件
            _this._addContextmenuEventListener()
        }


        if (_this._option.enableMouseWheelToggleTab === true) {
            _this._registerMouseWheelToggleTabEvent();
        }


    }


    _addContextmenuEventListener() {
        let _this = this;

        //tab右键
        Util.delegate(_this._tab_wraper, 'contextmenu', 'button.nav-link', function (e) {
            e.preventDefault();

            let id = Quicktab.getTabId(this);

            //同时查看是否需要显示关闭选项
            if (_this._canRemoveTabById(id)) {
                _this._tab_contextmenu_wraper.querySelector('.close').style.display = 'block';
            } else {
                _this._tab_contextmenu_wraper.querySelector('.close').style.display = 'none';
            }

            _this._tab_contextmenu_wraper.style.top = `${e.clientY}px`;
            _this._tab_contextmenu_wraper.style.left = `${e.clientX}px`;
            _this._tab_contextmenu_wraper.style.display = 'block';

            //同时给iframe窗口禁用所有的鼠标事件
            _this._tab_container.classList.add(TAB_CONTEXTMENU_POINTER_EVENTS_CLASS_NAME);


            //如果此时下拉菜单是打开着的，需要给它关闭掉
            let tabActionsDropdown = bootstrap.Dropdown.getOrCreateInstance(_this._tab_container.querySelector('ul>li.dropdown>button'));
            tabActionsDropdown.hide();


            //把所有的id都给遍历给右键菜单的每一项
            _this._tab_contextmenu_wraper.querySelectorAll('button.list-group-item').forEach(function (item) {
                item.setAttribute(TAB_ID_KEY, id);
            })

        });

        //点击任何区域左键单击都关闭
        document.addEventListener('click', function () {
            _this._closeTabsContextmenu();
        })

        //内容区域的右键也要能关闭这个tab右键菜单
        Util.delegate(_this._tab_container, 'contextmenu', 'div.flex-grow-1', function (e) {
            e.preventDefault();
            _this._closeTabsContextmenu();
            _this._moreMenuDropdownHide();
        });


        //tab右键刷新左击处理
        Util.delegate(_this._tab_contextmenu_wraper, 'click', '.refresh', function (e) {
            e.preventDefault();

            console.log(_this._tab_contextmenu_wraper);

            _this.refreshTabById(this.getAttribute(TAB_ID_KEY));
            _this._closeTabsContextmenu()

        });


        //tab右键刷新右击处理
        Util.delegate(_this._tab_contextmenu_wraper, 'contextmenu', '.refresh', function (e) {
            e.preventDefault();

            console.log(_this._tab_contextmenu_wraper)

            _this.refreshTabById(this.getAttribute(TAB_ID_KEY));
            _this._closeTabsContextmenu()

        });


        //右键菜单-关闭左击处理
        Util.delegate(_this._tab_contextmenu_wraper, 'click', '.close', function (e) {
            e.preventDefault();

            _this.closeTabById(this.getAttribute(TAB_ID_KEY));
            _this._closeTabsContextmenu();

        });

        //右键菜单-关闭右击处理
        Util.delegate(_this._tab_contextmenu_wraper, 'contextmenu', '.close', function (e) {
            e.preventDefault();
            _this.closeTabById(this.getAttribute(TAB_ID_KEY));
            _this._closeTabsContextmenu();
        });


        //右键菜单-关闭其它左击处理
        Util.delegate(_this._tab_contextmenu_wraper, 'click', '.close-other', function (e) {
            e.preventDefault();

            _this.closeAlLTabsExceptById(Quicktab.getTabId(this));
            _this._closeTabsContextmenu()
        });

        //右键菜单-关闭其它右击处理
        Util.delegate(_this._tab_contextmenu_wraper, 'contextmenu', '.close-other', function (e) {
            e.preventDefault();
            _this.closeAlLTabsExceptById(Quicktab.getTabId(this));
            _this._closeTabsContextmenu()
        });


    }

    _restoreTabs() {


        //判断选项是否开启缓存
        if (this._isCache()) {

            let cacheTabs = this._getCacheTabs();

            if(cacheTabs.length !== 0){
                this._restoreTabsHandle(cacheTabs, this._getActiveTabFromCache().id);
            }else{
                //要缓存
                this._restoreTabsHandle(this._option.tabs,null,true);
            }

        } else {
            //删除缓存
            Storage.removeBoth(this._tab_cache_key);
            //回显处理程序
             this._restoreTabsHandle(this._option.tabs);


        }
    }


    closeTabById(id) {

        let tab = this.getTabById(id);
        if (tab.classList.contains('active')) {//判断是否是激活的tab
            //把active传递给其它的tab
            let willActiveId = null;
            if (tab.nextElementSibling instanceof HTMLElement) {//后面有tab
                //得到后面tab的pageid作为将要激活的tab id


                willActiveId = Quicktab.getTabId(tab.nextElementSibling);
            } else if (tab.previousElementSibling instanceof HTMLElement) { //看看前面是否有tab

                willActiveId = Quicktab.getTabId(tab.previousElementSibling);
            }

            //先删除再滚动
            this._removeTabById(id)
            if (willActiveId) {//如果该id存在就激活
                //激活
                this.activeTabById(willActiveId);
            }

        } else { //不是激活的tab直接删除

            this._removeTabById(id)
        }


    }


    _removeTabById(id) {

        //删除tab
        this.getTabById(id).remove();

        //删除tab面板
        let tabPane = this.getTabPaneById(id);
        if (tabPane instanceof HTMLElement) {
            tabPane.remove();
        }

        // 删除缓存里面的tab
        this._removeCacheTabById(id);

    }


    /**
     * 添加tab进缓存
     * @param option
     * @private
     */
    _addTabToCache(option) {
        if (this._isCache()) {
            //先取
            let tabs = this._getCacheTabs();
            //追加属性 active
            option.active = false;
            tabs.push(option)
            //重新设置回去
            this.storage.set(this._tab_cache_key, tabs);
        }
    }

    _getCacheTabs() {
        if (this._isCache()) {
            let tabs = this.storage.get(this._tab_cache_key);
            return tabs === null ? [] : tabs;
        }
    }


    _isCache() {
        return this._option.cache !== null;
    }


    /**
     *
     * @param tabs tab数组
     * @param activeId   待激活的id,不填默认激活tabs的最后一项
     * @param cache   是否添加进缓存 默认：false  true：同时添加进本地缓存
     * @private
     */
    _restoreTabsHandle(tabs, activeId = null, cache = false) {
        let _this = this;

        //接受tab数组
        let opTabs = tabs;

        for (const tab of opTabs) {//先判断是否有id属性
            if (!Object.prototype.hasOwnProperty.call(tab, 'id')) {
                tab.id = md5(tab.url);
            }
        }

        //创建两个虚拟节点
        let tabFrag = document.createDocumentFragment();
        //这里只添加所有的tab，不添加iframe,否则全部加载iframe将会卡爆(重点优化)
        opTabs.forEach((option) => {
            if(cache===true){
                _this._addTabToCache(option);
            }
            let tabHTML = _this._getFormatTabStr(option);
            tabFrag.appendChild(Util.createNode(tabHTML));
        });
        //添加到tab的容器里面
        _this._tab_wraper.appendChild(tabFrag);

        //同时还需要判断如果开启缓存则需要缓存传递过来的tab
        // if(_this._isCache() && _this._getCacheTabs().length !== 0){
        //     opTabs.forEach((option) => {
        //         _this._addTabToCache(option);
        //     });
        // }


        if (activeId !== null) {
            _this.activeTabById(activeId);
        } else {
            //激活数组里最后一个tab
            _this.activeTabById(opTabs.at(-1).id);
        }

    }


    _getFormatTabStr(option) {

        return option.close === true ? this._formatString(TAB_WITH_CLOSE_TPL, option) : this._formatString(TAB_TPL, option);
    }


    _getActiveTabFromCache() {
        if (this._isCache()) {
            let tab = null;
            this._getCacheTabs().forEach((option) => {
                if (Object.prototype.hasOwnProperty.call(option, 'active') && option.active === true) {
                    tab = option;
                }
            })
            return tab;
        }
    }


    _activeCacheTabById(id) {

        if (this._isCache()) {
            let cacheTabs = this._getCacheTabs();
            cacheTabs.forEach((option) => {
                option.active = option.id === id;
            });
            this.storage.set(this._tab_cache_key, cacheTabs)
        }
    }


    _addTabPaneById(id, active = true) {
        let tab = this.getTabById(id);
        let option = {id: Quicktab.getTabId(tab), url: Quicktab.getTabUrl(tab)};
        let tabPaneNode = Util.createNode(this._formatString(TAB_PANE_TPL, option));

        //如果不是激活的直接给它添加上hide类
        if (active === false) {
            tabPaneNode.classList.add(TAB_PANE_HIDE_CLASS_NAME);
        }

        this._tab_pane_wraper.appendChild(tabPaneNode);

    }


    _removeCacheTabById(id) {
        if (this._isCache()) {
            let cacheTabs = this._getCacheTabs();
            cacheTabs.forEach((tab, index) => {
                if (tab.id === id) {
                    cacheTabs.splice(index, 1)
                }
            })
            //重新设置回去
            this.storage.set(this._tab_cache_key, cacheTabs)
        }
    }


    refreshTabById(id) {

        let tabPane = this.getTabPaneById(id);
        if (!(tabPane instanceof HTMLElement)) {//没有的话需要添加tab面板还有注册iframe的事件
            this._addTabPaneById(id, false);
            this._addIframeLoadEventById(id);
        } else {//如果有就正常刷新逻辑
            //只需要把该遮罩层插入,然后刷新即可
            let loading = this.getTabPaneById(id).querySelector('div');
            if (loading instanceof HTMLElement) {
                loading.remove();
            }
            this.getTabPaneById(id).insertAdjacentHTML('beforeEnd', TAB_LOADING_TPL);
            //在这里还需要判断是否存在跨域的情况，否则刷新功能会报错
            let iframe = this.getIFrameById(id);
            let url = iframe.getAttribute('src');
            if (Quicktab.canAccessIFrame(iframe)) {
                iframe.contentWindow.location.reload();
            } else {
                iframe.setAttribute('src', url);// 跨域状况下，重新设置url
            }
        }
    }


    _canRemoveTabById(id) {
        return (this.getTabById(id).querySelector(`.${TAB_CLOSE_BTN_ICON_CLASS_NAME}`) instanceof HTMLElement);
    }


    _closeTabsContextmenu() {
        this._tab_contextmenu_wraper.style.display = 'none';
        this._tab_container.classList.remove(TAB_CONTEXTMENU_POINTER_EVENTS_CLASS_NAME);
    }

    //更多操作处理弹出下拉菜单时让鼠标点击tab面板区域可以关闭
    _moreMenuEvent() {
        let _this = this;
        //更多操作的下拉菜单
        const tabActionsDropdown = _this._tab_container.querySelector('ul>li.dropdown>button');
        tabActionsDropdown.addEventListener('show.bs.dropdown', () => {
            _this._tab_container.classList.add(TAB_MORE_ACTION_DROPDOWNS_POINTER_EVENTS_CLASS_NAME);
        })
        //更多操作下拉菜单关闭移除
        tabActionsDropdown.addEventListener('hide.bs.dropdown', () => {
            _this._tab_container.classList.remove(TAB_MORE_ACTION_DROPDOWNS_POINTER_EVENTS_CLASS_NAME);
        })
    }

    _moreMenuDropdownHide() {
        bootstrap.Dropdown.getOrCreateInstance(this._tab_container.querySelector('ul>li.dropdown>button')).hide();
    }


    scrollToActiveTab() {
        let activeTab = this.getActiveTab();
        if (activeTab instanceof HTMLElement) {
            this.scrollToTabById(Quicktab.getTabId(activeTab));
        }
    }


    refreshActiveTab() {
        this.refreshTabById(Quicktab.getTabId(this.getActiveTab()));
    }


    setTabPaneIFrame(callBack) {

        let tabsArr = [];

        let iframes = this._getIFrames()

        iframes.forEach(function (iframe) {
            let iframeAttr = {};
            iframeAttr.iframe = iframe;
            if (Quicktab.canAccessIFrame(iframe)) {
                iframeAttr.canAccessIFrame = true;
                iframeAttr.iframeWindow = iframe.contentWindow;
            } else {
                iframeAttr.canAccessIFrame = false;
                iframeAttr.iframeWindow = null;
            }
            tabsArr.push(iframeAttr);
        })
        callBack(tabsArr);
    }

    _getIFrames() {
        return this._tab_pane_wraper.querySelectorAll('iframe');
    }


    fullscreen() {
        if (this._option.enableFullscreen === true) {
            document.documentElement.style.overflow = 'hidden';
            this._tab_container.classList.add(TAB_FULLSCREEN_CLASS_NAME);
            this._tab_container.querySelector(`.${TAB_FULLSCREEN_TRIGGER_CLASS_NAME}`).innerHTML = '<i class="bi bi-fullscreen-exit"></i>'
        }
    }


    exitFullscreen() {
        if (this._option.enableFullscreen === true) {
            document.documentElement.style.overflow = 'auto';
            this._tab_container.classList.remove(TAB_FULLSCREEN_CLASS_NAME);
            this._tab_container.querySelector(`.${TAB_FULLSCREEN_TRIGGER_CLASS_NAME}`).innerHTML = '<i class="bi bi-arrows-fullscreen"></i>'
            //回到当前
            this.scrollToActiveTab();
        }
    }

    leftScroll() {
        this._tab_wraper.scrollLeft = this._tab_wraper.scrollLeft - this._tab_wraper.offsetWidth;
    }

    rightScroll() {
        this._tab_wraper.scrollLeft = this._tab_wraper.scrollLeft + this._tab_wraper.offsetWidth;
    }

    _dataAttrAssign() {
        //必须要判断是否有data属性
        let op_tabs = this._tab_container.getAttribute('data-qt-tabs');
        if (op_tabs !== null) {
            this._option = Object.assign({}, this._option, {tabs: JSON.parse(op_tabs)});
        }
    }


    _addIframeLoadEventById(id) {
        let _this = this;

        let iframe = _this.getIFrameById(id);
        //必须得用onload事件来监听，这样刷新页面的时候，也会走该事件监听
        iframe.onload = function () {

            //触发
            if (_this.listeners.indexOf('iframeLoaded') > -1) {
                _this._emit({type: 'iframeLoaded', target: iframe, ifContentWindow: iframe.contentWindow});
            }


            //fade out
            _this.getTabPaneById(id).querySelector('div').classList.replace('show', 'hide')


            let loading = _this.getTabPaneById(id).querySelector('div');
            //监听loadding层执行完毕后给它移除掉。
            loading.addEventListener('transitionend', function () {

                //加载层淡出过渡执行完毕
                if (_this.listeners.indexOf('loadingTransitionend') > -1) {
                    _this._emit({type: 'loadingTransitionend', target: loading});
                }
                this.remove();
            });
        };
    }


    _formatMainHtmlStr() {
        let tab_main_html_str = '';
        if (this._option.enableFullscreen === true) {
            tab_main_html_str = this._formatString(TAB_MAIN_TPL, {tab_fullscreen: TAB_FULLSCREEN_TPL});
        } else {
            tab_main_html_str = this._formatString(TAB_MAIN_TPL, {tab_fullscreen: ''});
        }
        return tab_main_html_str;
    }


    _setContainerWH() {

        if (Util.isString(this._option.height) && this._option.height.trim().length !== 0) {
            this._tab_container.style.setProperty('height', this._option.height, 'important');
        }
        if (Util.isString(this._option.width) && this._option.width.trim().length !== 0) {
            this._tab_container.style.setProperty('width', this._option.width, 'important');
        }
        if (Util.isString(this._option.minHeight) && this._option.minHeight.trim().length !== 0) {
            this._tab_container.style.setProperty('min-height', this._option.minHeight, 'important');
        }
    }


    _registerMouseWheelToggleTabEvent() {
        let _this = this;
        this._tab_wraper.addEventListener('wheel', Util.debounce(function (e) {
            e.preventDefault();
            e.stopPropagation();


            let activeTab = _this.getActiveTab();

            //滚动的时候必须关闭tab的右键菜单
            _this._closeTabsContextmenu();

            let deltaY = e.deltaY;
            if (deltaY < 0) {//向左滚动

                //获取它前一个tab
                let preTab = activeTab.previousElementSibling;
                if (preTab instanceof HTMLElement) {

                    _this.activeTabById(Quicktab.getTabId(preTab))
                }

            } else if (deltaY > 0) {//向右滚动
                let nextTab = activeTab.nextElementSibling;
                if (nextTab instanceof HTMLElement) {

                    _this.activeTabById(Quicktab.getTabId(nextTab))
                }

            }

        }, 100));
    }


    closeActiveTab() {
        let id = Quicktab.getTabId(this.getActiveTab());
        if (this._canRemoveTabById(id)) {//判断能不能删除
            this.closeTabById(id)
        }
    }


    closeAlLTabsExceptById(id) {
        let _this = this;
        this._tab_wraper.querySelectorAll('button.nav-link').forEach((tab) => {
            let forEachId = Quicktab.getTabId(tab);
            if (forEachId !== id && _this._canRemoveTabById(forEachId)) {//如果不相等，且tab可以被删除的情况下
                _this.closeTabById(forEachId);
            }
        });
    }


    closeAlLTabsExceptActiveTab() {
        this.closeAlLTabsExceptById(Quicktab.getTabId(this.getActiveTab()));
    }


    closeAllTabs() {
        let _this = this;

        this._tab_wraper.querySelectorAll('button.nav-link').forEach((tab) => {
            let id = Quicktab.getTabId(tab);
            if (_this._canRemoveTabById(id)) {
                _this._removeTabById(id);
            }
        });

        //选中那些禁止删除的取第一个,将其激活
        let lastTab = this._tab_wraper.lastChild;
        if (lastTab instanceof HTMLElement) {
            _this.activeTabById(Quicktab.getTabId(lastTab));
        }
    }

}

export default Quicktab